:root {
  color-scheme: light dark;
  color: #ffffffde;
  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #242424;
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  font-weight: 400;
  line-height: 1.5;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  overflow-x: hidden;
}

#app {
  width: 100vw;
  min-height: 100vh;
}

.container {
  width: 100%;
  min-height: 100vh;
  position: relative;
}

.hero-section {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
  position: relative;
}

#scene-container {
  z-index: 1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.content {
  z-index: 2;
  text-align: center;
  pointer-events: none;
  padding: 2rem;
  position: relative;
}

.title {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(45deg, #646cff, #9f9fff) text;
  margin-bottom: .5rem;
  font-size: 3.2em;
  line-height: 1.1;
}

.subtitle {
  color: #ffffffb3;
  margin-bottom: 1rem;
  font-size: 1.2em;
}

.scroll-hint {
  color: #fff9;
  font-size: 1em;
  font-style: italic;
  animation: 2s ease-in-out infinite pulse;
}

@keyframes pulse {
  0%, 100% {
    opacity: .6;
  }

  50% {
    opacity: 1;
  }
}

.scroll-sections {
  z-index: 3;
  background: linear-gradient(135deg, #1a1a1a 0%, #2a2a2a 100%);
  position: relative;
}

.section {
  text-align: center;
  border-bottom: 1px solid #ffffff1a;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 4rem 2rem;
  display: flex;
}

.section h2 {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(45deg, #646cff, #9f9fff) text;
  margin-bottom: 1rem;
  font-size: 2.5em;
}

.section p {
  color: #fffc;
  max-width: 600px;
  font-size: 1.2em;
  line-height: 1.6;
}

a {
  color: #646cff;
  text-decoration: inherit;
  font-weight: 500;
}

a:hover {
  color: #535bf2;
}

h1 {
  font-size: 3.2em;
  line-height: 1.1;
}

.logo {
  will-change: filter;
  height: 6em;
  padding: 1.5em;
  transition: filter .3s;
}

.logo:hover {
  filter: drop-shadow(0 0 2em #646cffaa);
}

.logo.vanilla:hover {
  filter: drop-shadow(0 0 2em #3178c6aa);
}

.card {
  padding: 2em;
}

.read-the-docs {
  color: #888;
}

button {
  cursor: pointer;
  background-color: #1a1a1a;
  border: 1px solid #0000;
  border-radius: 8px;
  padding: .6em 1.2em;
  font-family: inherit;
  font-size: 1em;
  font-weight: 500;
  transition: border-color .25s;
}

button:hover {
  border-color: #646cff;
}

button:focus, button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}

@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #fff;
  }

  .scroll-sections {
    background: linear-gradient(135deg, #f5f5f5 0%, #e5e5e5 100%);
  }

  .section {
    border-bottom: 1px solid #0000001a;
  }

  .section h2 {
    -webkit-text-fill-color: transparent;
    background: linear-gradient(45deg, #646cff, #535bf2) text;
  }

  .section p {
    color: #000c;
  }

  a:hover {
    color: #747bff;
  }

  button {
    background-color: #f9f9f9;
  }
}
